import React, { useState, useCallback, useEffect, useMemo, useRef } from "react";
import axios from "axios";

import { createLetter } from "services";
import {
  formatDate,
  sanitizeEmail,
  sanitizeString,
  isValidPngDataUrl,
} from "utils";
import { COUNTRIES_API, GEOLOCATION_API, LocalStorageKeys } from "constant";

import {
  Step,
  Country,
  FormData,
  FormStep,
  PreviewStep,
  SuccessStep,
} from "./components";

const getInitialFormData = (): FormData => {
  if (typeof window === "undefined") {
    return {
      fullName: "",
      email: "",
      city: "",
      country: "",
      state: "",
      zipCode: "",
      message: "",
    };
  }

  try {
    const saved = localStorage.getItem(LocalStorageKeys.LETTER_DRAFT_FORM_DATA);
    if (saved) {
      return JSON.parse(saved) as FormData;
    }
  } catch {
    // Ignore parse errors
  }

  return {
    fullName: "",
    email: "",
    city: "",
    country: "",
    state: "",
    zipCode: "",
    message: "",
  };
};

const getInitialSignature = (): string | null => {
  if (typeof window === "undefined") return null;

  try {
    return localStorage.getItem(LocalStorageKeys.LETTER_DRAFT_SIGNATURE);
  } catch {
    return null;
  }
};

const WriteLetterForm = () => {
  const [step, setStep] = useState(Step.Form);
  const [formData, setFormData] = useState<FormData>(getInitialFormData);
  const isInitialMount = useRef(true);

  const currentDate = useMemo(() => formatDate(new Date()), []);

  const [countries, setCountries] = useState<Country[]>([]);
  const [signature, setSignature] = useState<string | null>(getInitialSignature);

  const [error, setError] = useState<string | null>(null);
  const [loadError, setLoadError] = useState<string | null>(null);

  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isLoadingLocation, setIsLoadingLocation] = useState(true);

  useEffect(() => {
    const source = axios.CancelToken.source();

    const fetchCountries = async () => {
      try {
        const { data } = await axios.get<
          { name: { common: string }; cca2: string }[]
        >(COUNTRIES_API, { cancelToken: source.token });

        const sortedCountries = data
          .map((c) => ({
            name: c.name.common,
            code: c.cca2,
          }))
          .sort((a, b) => a.name.localeCompare(b.name));
        setCountries(sortedCountries);
      } catch (err) {
        if (!axios.isCancel(err)) {
          setLoadError("Failed to load countries list");
        }
      }
    };

    fetchCountries();

    return () => source.cancel();
  }, []);

  useEffect(() => {
    const source = axios.CancelToken.source();

    const fetchLocation = async () => {
      try {
        const { data } = await axios.get<{
          city?: string;
          country_name?: string;
        }>(GEOLOCATION_API, { cancelToken: source.token });
        setFormData((prev) => ({
          ...prev,
          city: prev.city || data.city || "",
          country: prev.country || data.country_name || "",
        }));
      } catch {
        // Location detection is optional
      } finally {
        setIsLoadingLocation(false);
      }
    };

    fetchLocation();

    return () => source.cancel();
  }, []);

  // Save form data to localStorage
  useEffect(() => {
    if (isInitialMount.current) {
      isInitialMount.current = false;
      return;
    }

    try {
      localStorage.setItem(
        LocalStorageKeys.LETTER_DRAFT_FORM_DATA,
        JSON.stringify(formData)
      );
    } catch {
      // Ignore storage errors
    }
  }, [formData]);

  // Save signature to localStorage
  useEffect(() => {
    try {
      if (signature) {
        localStorage.setItem(LocalStorageKeys.LETTER_DRAFT_SIGNATURE, signature);
      } else {
        localStorage.removeItem(LocalStorageKeys.LETTER_DRAFT_SIGNATURE);
      }
    } catch {
      // Ignore storage errors
    }
  }, [signature]);

  const handleChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
      const { name, value } = e.target;
      setFormData((prev) => ({ ...prev, [name]: value }));
    },
    []
  );

  const handleCountryChange = useCallback((country: Country) => {
    setFormData((prev) => {
      const isNewCountryUSA = country.name === "United States";
      const wasUSA = prev.country === "United States";

      // Clear state and zipCode when changing from USA to another country
      if (wasUSA && !isNewCountryUSA) {
        return { ...prev, country: country.name, state: "", zipCode: "" };
      }

      return { ...prev, country: country.name };
    });
  }, []);

  const handleSignatureChange = useCallback((sig: string | null) => {
    setSignature(sig);
  }, []);

  const handlePreview = useCallback(() => {
    setStep(Step.Preview);
    window.history.pushState({ step: "preview" }, "", window.location.href);
  }, []);

  const handleBack = useCallback(() => {
    setStep(Step.Form);
  }, []);

  useEffect(() => {
    const handlePopState = () => {
      if (step === Step.Preview) {
        setStep(Step.Form);
      }
    };

    window.addEventListener("popstate", handlePopState);
    return () => window.removeEventListener("popstate", handlePopState);
  }, [step]);

  const handleSubmit = useCallback(async () => {
    if (!signature) return;

    if (!isValidPngDataUrl(signature)) {
      setError("Invalid signature format");
      return;
    }

    setIsSubmitting(true);
    setError(null);

    const sanitizedFullName = sanitizeString(formData.fullName);
    const nameParts = sanitizedFullName.split(/\s+/).filter(Boolean);
    const firstName = nameParts[0] || "";
    const lastName = nameParts.slice(1).join(" ") || "";

    try {
      await createLetter({
        date: currentDate,
        signature: signature,
        lastName: sanitizeString(lastName),
        city: sanitizeString(formData.city),
        email: sanitizeEmail(formData.email),
        firstName: sanitizeString(firstName),
        country: sanitizeString(formData.country),
        state: formData.state ? sanitizeString(formData.state) : undefined,
        zipCode: formData.zipCode
          ? sanitizeString(formData.zipCode)
          : undefined,
        message: sanitizeString(formData.message),
      });

      setStep(Step.Success);
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to submit letter");
    } finally {
      setIsSubmitting(false);
    }
  }, [formData, signature, currentDate]);

  const handleReset = useCallback(() => {
    // Clear localStorage when creating a new letter
    try {
      localStorage.removeItem(LocalStorageKeys.LETTER_DRAFT_FORM_DATA);
      localStorage.removeItem(LocalStorageKeys.LETTER_DRAFT_SIGNATURE);
    } catch {
      // Ignore storage errors
    }

    setFormData({
      fullName: "",
      email: "",
      city: "",
      country: "",
      state: "",
      zipCode: "",
      message: "",
    });
    setSignature(null);
    setError(null);
    setStep(Step.Form);
  }, []);

  if (step === Step.Success) {
    return <SuccessStep onReset={handleReset} />;
  }

  if (step === Step.Preview) {
    return (
      <PreviewStep
        formData={formData}
        currentDate={currentDate}
        signature={signature}
        isSubmitting={isSubmitting}
        error={error}
        onSignatureChange={handleSignatureChange}
        onBack={handleBack}
        onSubmit={handleSubmit}
      />
    );
  }

  return (
    <FormStep
      formData={formData}
      countries={countries}
      currentDate={currentDate}
      isLoadingLocation={isLoadingLocation}
      loadError={loadError}
      onChange={handleChange}
      onCountryChange={handleCountryChange}
      onPreview={handlePreview}
    />
  );
};

export { WriteLetterForm };
