import React from "react";
import { videos, posts, MediaContent } from "constant";

import { RowSections } from "components/layouts";

import { Section } from "components/layouts/RowSections/Style";
import { Link, Item, ItemWrap, Play, ItemImage, ItemTitle } from "./Style";

function Media() {
  return (
    <Section id="media" withPaddings>
      <RowSections id="video" title="Learn more" withPaddings={false}>
        {videos.map((video: MediaContent) => (
          <Link href={video.url} key={video.id}>
            <Item key={video.id}>
              <ItemWrap>
                <ItemImage src={video.thumbnail} alt={video.title} />
                <Play name="play" size="xl" />
              </ItemWrap>
              <ItemTitle>{video.title}</ItemTitle>
            </Item>
          </Link>
        ))}
      </RowSections>
      <RowSections id="posts" withPaddings={false}>
        {posts.map((post: MediaContent) => (
          <Link href={post.url} key={post.id}>
            <Item key={post.id}>
              <ItemWrap>
                <ItemImage src={post.thumbnail} alt={post.title} />
              </ItemWrap>
              <ItemTitle>{post.title}</ItemTitle>
            </Item>
          </Link>
        ))}
      </RowSections>
    </Section>
  );
}

export { Media };
