import React, { useMemo } from "react";
import Helmet, { HelmetProps } from "react-helmet";

type MetaProps = JSX.IntrinsicElements["meta"];
type Props = HelmetProps & {
  description?: string;
  lang?: string;
  type?: string;
};

function SEO({
  meta = [],
  title = "",
  lang = "en",
  type = "website",
  description = "",
  titleTemplate = "%s",
  ...props
}: Props) {
  const metaProp = useMemo<Array<MetaProps>>(() => {
    const metaData: Array<MetaProps> = [
      {
        property: `og:title`,
        content: title,
      },
      {
        name: `twitter:title`,
        content: title,
      },
      {
        name: `description`,
        content: description,
      },
      {
        property: `og:description`,
        content: description,
      },
      {
        name: `twitter:description`,
        content: description,
      },
      {
        property: `og:type`,
        content: type,
      },
      {
        name: `twitter:card`,
        content: `summary`,
      },
      {
        property: `og:sitename`,
        content: title,
      },
    ];

    return metaData.concat(meta);
  }, [description, meta, description, type]);

  return (
    <Helmet
      {...props}
      title={title}
      titleTemplate={titleTemplate}
      htmlAttributes={{
        lang,
      }}
      meta={metaProp}
    />
  );
}

export { SEO };
