import React from "react";

type Props = {
  href: string;
  ariaLabel?: string;
  className?: string;
  children: React.ReactNode;
};

function Href({ href, children, ariaLabel, className }: Props) {
  return (
    <a
      href={href}
      className={className}
      aria-label={ariaLabel}
      target="_blank"
      rel="noopener noreferrer"
    >
      {children}
    </a>
  );
}

export { Href };
