import axios, { AxiosError } from "axios";
import * as Sentry from "@sentry/gatsby";

import { LETTER_API } from "constant";

export interface LetterData {
  firstName: string;
  lastName: string;
  city: string;
  date: string;
  email: string;
  state?: string;
  country: string;
  zipCode?: string;
  message: string;
  signature: string;
}

export interface CreateLetterResponse {
  id: string;
}

const MAX_RETRIES = 3;
const RETRY_DELAY_MS = 2000;

const delay = (ms: number) => new Promise((resolve) => setTimeout(resolve, ms));

const isRetryableError = (error: unknown): boolean => {
  if (axios.isAxiosError(error)) {
    const axiosError = error as AxiosError;
    // Retry on network errors or 5xx server errors
    if (!axiosError.response) return true;
    const status = axiosError.response.status;
    return status >= 500 && status < 600;
  }
  return false;
};

export const createLetter = async (
  data: LetterData
): Promise<CreateLetterResponse> => {
  const headers = {
    "Content-Type": "application/json",
  };

  let lastError: unknown;

  Sentry.setContext("letter_form", {
    firstName: data.firstName,
    lastName: data.lastName,
    email: data.email,
    city: data.city,
    country: data.country,
    state: data.state,
    zipCode: data.zipCode,
    messageLength: data.message.length,
    hasSignature: !!data.signature,
  });

  for (let attempt = 1; attempt <= MAX_RETRIES; attempt++) {
    try {
      const response = await axios.post<CreateLetterResponse>(
        LETTER_API,
        data,
        {
          headers,
        }
      );

      return response.data;
    } catch (error) {
      lastError = error;

      Sentry.captureException(error, {
        tags: {
          action: "create_letter",
          attempt: attempt.toString(),
          isRetryable: isRetryableError(error).toString(),
        },
        extra: {
          formData: {
            firstName: data.firstName,
            lastName: data.lastName,
            email: data.email,
            city: data.city,
            country: data.country,
            state: data.state,
            zipCode: data.zipCode,
            message: data.message,
          },
          errorDetails: axios.isAxiosError(error)
            ? {
                status: error.response?.status,
                statusText: error.response?.statusText,
                responseData: error.response?.data,
                requestUrl: error.config?.url,
                requestMethod: error.config?.method,
              }
            : undefined,
        },
      });

      if (!isRetryableError(error) || attempt === MAX_RETRIES) {
        throw error;
      }

      await delay(RETRY_DELAY_MS * attempt);
    }
  }

  throw lastError;
};
