/**
 * Sanitizes a string by removing or escaping potentially dangerous characters
 * while preserving legitimate content.
 */
export function sanitizeString(input: string): string {
  if (!input || typeof input !== "string") {
    return "";
  }

  return (
    input
      .trim()
      // Remove null bytes
      .replace(/\0/g, "")
      // Remove control characters except newlines and tabs
      .replace(/[\x00-\x08\x0B\x0C\x0E-\x1F\x7F]/g, "")
      // Limit consecutive whitespace
      .replace(/[ \t]{3,}/g, "  ")
  );
}

/**
 * Validates that a string is a valid PNG data URL from canvas.toDataURL()
 */
export function isValidPngDataUrl(dataUrl: string): boolean {
  if (!dataUrl || typeof dataUrl !== "string") {
    return false;
  }

  // Must start with the correct data URL prefix for PNG
  if (!dataUrl.startsWith("data:image/png;base64,")) {
    return false;
  }

  // Extract the base64 part
  const base64Part = dataUrl.slice("data:image/png;base64,".length);

  // Check if it's valid base64
  if (!/^[A-Za-z0-9+/]+=*$/.test(base64Part)) {
    return false;
  }

  // Check reasonable size (max ~500KB for a signature)
  if (base64Part.length > 700000) {
    return false;
  }

  return true;
}

/**
 * Sanitizes email by removing potentially dangerous characters
 */
export function sanitizeEmail(email: string): string {
  if (!email || typeof email !== "string") {
    return "";
  }

  return (
    email
      .trim()
      .toLowerCase()
      // Remove any characters that shouldn't be in an email
      .replace(/[<>'";\\\x00-\x1F\x7F]/g, "")
  );
}
