import React from "react";
import Helmet from "react-helmet";
import { useStaticQuery, graphql } from "gatsby";

import styled, { ThemeProvider } from "styled-components/macro";
import { theme, GlobalStyle } from "theme";

import { link } from "constant";
import { CookieConsent } from "components/feature";

const Wrap = styled.div`
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: ${(props) => props.theme.color.pageBg};
`;

type Props = {
  children: React.ReactNode;
};

const LayoutMain = ({ children }: Props) => {
  const data = useStaticQuery(graphql`
    query LayoutMainQuery {
      site {
        siteMetadata {
          siteUrl
          title
          description
          author
          themeColor
          ogType
          ogSiteName
          ogLocale
          ogImage
          ogImageType
          ogImageWidth
          ogImageHeight
          twitterCard
          twitterDomain
          twitterImage
          twitterImageAlt
        }
      }
    }
  `);
  return (
    <ThemeProvider theme={theme}>
      <Wrap>
        <Helmet
          defaultTitle={data.site.siteMetadata.title}
          titleTemplate={`%s | ${data.site.siteMetadata.title}`}
          link={[
            {
              rel: "canonical",
              href: data.site.siteMetadata.siteUrl,
            },
            {
              sizes: "any",
              rel: "icon",
              href: `${link.ASSETS_HOST}/icons/favicon.ico`,
            },
            {
              rel: "icon",
              type: "image/svg+xml",
              href: `${link.ASSETS_HOST}/icons/favicon.svg`,
            },
            {
              rel: "apple-touch-icon",
              href: `${link.ASSETS_HOST}/icons/apple-touch-icon.png`,
            },
            {
              rel: "preconnect",
              href: "https://fonts.googleapis.com",
            },
            {
              rel: "preconnect",
              href: 'https://fonts.gstatic.com" crossorigin',
            },
            {
              rel: "stylesheet",
              href: "https://fonts.googleapis.com/css2?family=Manrope:wght@200..800&display=swap",
            },
            {
              rel: "stylesheet",
              href: "https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap",
            },
          ]}
          meta={[
            {
              name: "description",
              content: data.site.siteMetadata.description,
            },
            {
              name: "author",
              content: data.site.siteMetadata.author,
            },
            {
              name: "msapplication-TileColor",
              content: data.site.siteMetadata.msapplicationTileColor,
            },
            { name: "theme-color", content: data.site.siteMetadata.themeColor },
            { property: "og:type", content: data.site.siteMetadata.ogType },
            {
              property: "og:site_name",
              content: data.site.siteMetadata.ogSiteName,
            },

            {
              property: "og:locale",
              content: data.site.siteMetadata.ogLocale,
            },

            {
              property: "og:image",
              content: data.site.siteMetadata.ogImage,
            },
            {
              property: "og:image:secure_url",
              content: data.site.siteMetadata.ogImage,
            },
            {
              property: "og:image:type",
              content: data.site.siteMetadata.ogImageType,
            },
            {
              property: "og:image:width",
              content: data.site.siteMetadata.ogImageWidth,
            },
            {
              property: "og:image:height",
              content: data.site.siteMetadata.ogImageHeight,
            },

            {
              name: "twitter:card",
              content: data.site.siteMetadata.twitterCard,
            },
            // { name: 'twitter:creator', content: data.site.siteMetadata.twitterCreator },
            { name: "twitter:title", content: data.site.siteMetadata.title },
            {
              name: "twitter:description",
              content: data.site.siteMetadata.description,
            },
            {
              name: "twitter:image",
              content: data.site.siteMetadata.twitterImage,
            },
            {
              name: "twitter:image:src",
              content: data.site.siteMetadata.twitterImage,
            },
            {
              name: "twitter:domain",
              content: data.site.siteMetadata.twitterDomain,
            },
            {
              name: "twitter:image:alt",
              content: data.site.siteMetadata.twitterImageAlt,
            },
          ]}
        >
          <html lang="en" />
        </Helmet>
        <GlobalStyle />
        {children}
        <CookieConsent />
      </Wrap>
    </ThemeProvider>
  );
};

export { LayoutMain };
