import styled from "styled-components/macro";
import { Link } from "gatsby";
import { transparentize } from "polished";

export const Section = styled.section`
  padding: 4.8rem 1.6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: ${(props) => props.theme.color.transparent};
  ${(props) => props.theme.breakpoints.greaterThan("sm")`
    padding: 4.8rem;
  `};
  ${(props) => props.theme.breakpoints.greaterThan("md")`
    padding: 5.6rem 4rem;
  `};
`;

export const Container = styled.div`
  position: relative;
  margin: 0 auto;
  width: 100%;
  max-width: 48.6rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3.2rem;
  z-index: ${(props) => props.theme.zIndex.minimal};
`;

export const Title = styled.h2`
  margin: 0;
  font-family: ${(props) => props.theme.font.family.primary};
  font-weight: ${(props) => props.theme.font.weight.bold};
  line-height: 1.2;
  letter-spacing: -0.096rem;
  text-align: center;
  color: ${(props) => props.theme.color.default};
  ${(props) => props.theme.breakpoints.greaterThan("sm")`
    font-size: 4.8rem;
    line-height: 1.17;
    letter-spacing: -0.032rem;
  `};
`;

export const TitleHint = styled.span`
  margin: 0;
  font-size: 8.4rem;
  line-height: 1.5;
  letter-spacing: -0.096rem;
  text-align: center;
  color: ${(props) => props.theme.color.default};
`;

export const Text = styled.p`
  margin: 0;
  font-weight: ${(props) => props.theme.font.weight.medium};
  font-size: 1.6rem;
  line-height: 1.5;
  letter-spacing: -0.041rem;
  text-align: center;
  color: ${(props) => props.theme.color.default};
  ${(props) => props.theme.breakpoints.greaterThan("sm")`
    font-size: 2rem;
    line-height: 1.6;
  `};
`;

export const StyledLink = styled(Link)`
  position: relative;

  margin: 0;
  padding: 1.2rem 2.4rem;

  width: auto;
  height: fit-content;

  display: inline-flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  flex: 0 0 auto;
  gap: 0.8rem;

  font-family: ${(props) => props.theme.font.family.default};
  font-weight: ${(props) => props.theme.font.weight.bold};

  font-size: 2.2rem;
  line-height: 1.5;

  text-decoration: none;

  color: ${(props) => props.theme.color.textTertiary};
  background-color: ${(props) => props.theme.color.default};
  border: none;
  border-radius: 6rem;

  transition: all 0.26s ease-in;

  overflow: hidden;
  outline: none;
  z-index: ${(props) => props.theme.zIndex.minimal};

  color: ${(props) => props.theme.color.textTertiary};
  background-color: ${(props) => props.theme.color.default};

  font-weight: ${(props) => props.theme.font.weight.semiBold};
  line-height: 0.85;
  letter-spacing: -0.03em;
  color: ${(props) => props.theme.color.white};
  background-color: ${(props) => props.theme.color.success};

  &:hover:not([disabled]),
  &:focus:not([disabled]),
  &:active:not([disabled]) {
    box-shadow: 0 1rem 1.8rem -0.9rem ${(props) => transparentize(0.63, props.theme.color.black)};
  }

  ${(props) => props.theme.breakpoints.greaterThan("lg")`
    padding: 2rem 3rem;
  `};
`;
