import React, { useMemo, useState } from "react";

import { Link } from "gatsby";

import { menu } from "constant";

import { Icon, LogoLink, ScrollTo } from "components/ui";

import {
  Nav,
  List,
  Wrap,
  Burger,
  Overlay,
  ListItem,
  Container,
  DonateLink,
  Connection,
  CustomLink,
  ListItemButton,
  ConnectionMobile,
} from "./Style";

const Header = () => {
  const [isOpenNav, setOpenNav] = useState(false);

  const handleClick = () => {
    setOpenNav(!isOpenNav);
    document.body.style.overflowY = isOpenNav ? "unset" : "hidden";
    document.body.style.overflowX = "hidden";
  };

  const handleClickMenu = () => {
    setOpenNav(!isOpenNav);
    document.body.style.overflowY = "unset";
  };

  const links = useMemo(() => {
    return [
      { to: menu.whoIs, href: "/#whoIs", text: "Who is Roman Storm?" },
      { to: menu.allegations, href: "/#allegations", text: "Allegations" },
      { to: menu.important, href: "/#important", text: "Why is it important?" },
      { to: menu.timeline, href: "/#timeline", text: "Timeline" },
      { to: menu.writeLetter, href: "/#write-letter", text: "Write a Letter" },
      { to: menu.community, href: "/#community", text: "Learn more" },
      { to: menu.faq, href: "/#faq", text: "FAQ" },
      // hidden links
      {
        to: menu.letter,
        href: "/#letter",
        text: "Congress Letter",
        hidden: true,
      },
      {
        to: menu.legalDefence,
        href: "/#legalDefence",
        text: "Legal Defence",
        hidden: true,
      },
      {
        to: menu.fightStory,
        href: "/#fightStory",
        text: "Storm Fight Story",
        hidden: true,
      },
      { to: menu.support, href: "/#support", text: "Support", hidden: true },
    ];
  }, []);

  return (
    <Wrap id="appHeader">
      <Container>
        <LogoLink />

        <Nav role="navigation" aria-label="Navigation" isOpen={isOpenNav}>
          <List>
            {links.map((link) => (
              <ListItem
                key={link.to}
                style={{ display: link.hidden ? "none" : "block" }}
              >
                <ScrollTo to={link.to} href={link.href}>
                  <ListItemButton onClick={handleClickMenu}>
                    <CustomLink>{link.text}</CustomLink>
                  </ListItemButton>
                </ScrollTo>
              </ListItem>
            ))}
            <ListItem>
              <Link to={menu.donate}>
                <ListItemButton onClick={handleClickMenu}>
                  <CustomLink>Donate</CustomLink>
                </ListItemButton>
              </Link>
            </ListItem>
          </List>

          <Connection>
            <DonateLink to={menu.donate}>
              Donate
              <Icon name="arrow" rotate="-90deg" />
            </DonateLink>
          </Connection>
        </Nav>

        <ConnectionMobile>
          <DonateLink to={menu.donate}>
            Donate
            <Icon name="arrow" rotate="-90deg" />
          </DonateLink>
        </ConnectionMobile>

        <Burger isOpen={isOpenNav} onClick={handleClick} />
        <Overlay
          isOpen={isOpenNav}
          onClick={handleClickMenu}
          role="presentation"
        />
      </Container>
    </Wrap>
  );
};

export { Header };
