import { keyframes } from "styled-components/macro";
import { theme } from "theme";

export const spin = keyframes`
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
`;

export const slideIn = keyframes`
  from {
    opacity: 0;
    transform: translateY(100%);
  }
`;

export const fadeIn = keyframes`
  17% {
    opacity:1;
  }
  25% {
    opacity:0;
  }
  92% {
    opacity:0;
  }
`;

export const shimmer = keyframes`
  0% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
`;

export const ripple = keyframes`
  0%  {
    transform: translate(-100%, -100%);
  }
  80%  {
    transform: translate(-100%, -100%) scale(50);
  }
  100% {
    transform: translate(-100%, -100%) scale(50);
    opacity: ${theme.opacity.invisible};
  }
`;

export const littleRipple = keyframes`
  0%  {
    transform: scale(1);
  }
  50%  {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
`;

export const scroll = keyframes`
  0% {
    transform: translateX(0);
  }
	100% {
    transform: translateX(calc(-38rem * 19.4))
  }
`;

export const scrollSupport = keyframes`
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(calc(-20rem * 11));
    }
`;

export const move = (width: number) => keyframes`
    100% {
        background-position: ${width}rem 0;
    }
`;

export const pulseAnimation = keyframes`
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.5;
  }
`;
