const ItemButton = css`
  position: relative;

  margin: 0;
  padding: 1.2rem 2.4rem;

  width: auto;
  height: fit-content;

  display: inline-flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  flex: 0 0 auto;
  gap: 0.8rem;

  font-family: ${(props) => props.theme.font.family.default};
  font-weight: ${(props) => props.theme.font.weight.bold};

  font-size: 1.8rem;
  line-height: 1.5;

  text-decoration: none;

  color: ${(props) => props.theme.color.textTertiary};
  background-color: ${(props) => props.theme.color.default};
  border: none;
  border-radius: 6rem;

  transition: all 0.26s ease-in;

  overflow: hidden;
  outline: none;
  z-index: ${(props) => props.theme.zIndex.minimal};

  ${(props) => props.theme.breakpoints.greaterThan("sm")`
    width: auto;
  `};
`;

export const DonateLink = styled(Link)`
  ${ItemButton};
  width: 100%;
  padding: 2rem;
  display: inline-flex;
  font-weight: ${(props) => props.theme.font.weight.semiBold};
  font-size: 2.2rem;
  color: ${(props) => props.theme.color.white};
  background-color: ${(props) => props.theme.color.success};

  svg {
    transition: all 0.26s ease;
  }

  &:hover {
    svg {
      transform: rotate(-45deg);
    }
  }

  ${(props) => props.theme.breakpoints.greaterThan("sm")`
    width: 20.8rem;
  `};

  &:hover:not([disabled]),
  &:focus:not([disabled]),
  &:active:not([disabled]) {
    box-shadow: 0 1rem 1.8rem -0.9rem ${(props) => transparentize(0.63, props.theme.color.black)};
  }
`;

