import React, { useEffect } from "react";
import { graphql, useStaticQuery } from "gatsby";

import { Href, Icon } from "components/ui";

import {
  Title,
  Section,
  Content,
  DocLink,
  ItemWrap,
  Container,
  TitleWrap,
  ItemTitle,
  Annotation,
  ContentWrap,
  HrefWrapper,
  VideoWrapper,
  ContainerLetter,
  ItemDescription,
  AnnotationTitle,
} from "./Style";

const DOC_LINK =
  "https://www.defieducationfund.org/_files/ugd/84ba66_903d8c40e59a422e81b3abe393ca9536.pdf";

const Letter = () => {
  const data = useStaticQuery(graphql`
    query {
      letterMp4: file(relativePath: { eq: "videos/letter.mp4" }) {
        publicURL
      }
      letterWebm: file(
        relativePath: { eq: "images/letter/letterPreview.webm" }
      ) {
        childImageSharp {
          fluid(maxWidth: 1000, quality: 100) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `);

  useEffect(() => {
    //@ts-ignore
    const handlePlay = (e) => {
      e.target.play().catch(() => {});
    };

    const videoElements = document.querySelectorAll("video");
    videoElements.forEach((video) => {
      video.addEventListener("loadedmetadata", handlePlay);
      video.addEventListener("canplay", handlePlay);
    });

    return () => {
      videoElements.forEach((video) => {
        video.removeEventListener("loadedmetadata", handlePlay);
        video.removeEventListener("canplay", handlePlay);
      });
    };
  }, []);

  return (
    <Section id="letter">
      <Container>
        <ContainerLetter>
          <TitleWrap>
            <Title>
              Congress Must Act: Storm Case Could Rewrite Digital Asset Law
            </Title>
          </TitleWrap>
          <Content>
            <ContentWrap>
              <VideoWrapper>
                <video
                  autoPlay
                  muted
                  loop
                  playsInline
                  style={{
                    width: "100%",
                    height: "auto",
                    borderRadius: "1.6rem",
                    backgroundColor: "#e7fbe6",
                  }}
                >
                  <source src={data.letterWebm} type="video/webm" />
                  <source src={data.letterMp4.publicURL} type="video/mp4" />
                  Your browser does not support HTML5 video.
                </video>
              </VideoWrapper>
              <AnnotationTitle>
                Tech industry, blockchain organizations, and legal experts urge
                Congress to&nbsp;address the U.S. Department
                of&nbsp;Justice&rsquo;s (DOJ) misapplication
                of&nbsp;18&nbsp;U.S.C. &sect;&nbsp;1960 to&nbsp;software
                developers like Roman Storm.
              </AnnotationTitle>
              <Annotation>
                <ItemWrap>
                  <ItemTitle>Clarify Regulatory Boundaries:</ItemTitle>
                  <ItemDescription>
                    The DOJ&rsquo;s interpretation of &sect;&nbsp;1960 conflicts
                    with{" "}
                    <a
                      href="https://www.fincen.gov/sites/default/files/2019-05/FinCEN%20Guidance%20CVC%20FINAL%20508.pdf"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      FinCEN&rsquo;s 2019 Guidance
                    </a>{" "}
                    , which excludes non-custodial software developers (who lack
                    control over user funds) from being classified
                    as&nbsp;&laquo;money transmitters.&raquo; This creates legal
                    ambiguity, jeopardizing developers who act in&nbsp;good
                    faith under existing rules.
                  </ItemDescription>
                </ItemWrap>
                <ItemWrap>
                  <ItemTitle>Legal Precedent Gap:</ItemTitle>
                  <ItemDescription>
                    No&nbsp;court has ever supported the DOJ&rsquo;s novel
                    interpretation of &sect;&nbsp;1960. Prosecuting Storm under
                    this untested theory risks criminalizing code development
                    itself, threatening innovation in&nbsp;blockchain and
                    beyond.
                  </ItemDescription>
                </ItemWrap>
              </Annotation>
            </ContentWrap>
          </Content>
          <HrefWrapper>
            <Href href={DOC_LINK}>
              <DocLink>
                Read full doc <Icon name="arrow" rotate="-90deg" />
              </DocLink>
            </Href>
          </HrefWrapper>
        </ContainerLetter>
      </Container>
    </Section>
  );
};

export { Letter };
