import React, {
  useRef,
  useEffect,
  useCallback,
  useImperativeHandle,
  forwardRef,
} from "react";

import { SignatureCanvas } from "../Style";

export interface SignaturePadRef {
  clear: VoidFunction;
}

interface SignaturePadProps {
  onSignatureChange: (signature: string | null) => void;
}

const SignaturePad = forwardRef<SignaturePadRef, SignaturePadProps>(
  ({ onSignatureChange }, ref) => {
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const isDrawingRef = useRef(false);

    const clearCanvas = useCallback(() => {
      const canvas = canvasRef.current;
      if (!canvas) return;

      const ctx = canvas.getContext("2d");
      if (!ctx) return;

      ctx.clearRect(0, 0, canvas.width, canvas.height);
      onSignatureChange(null);
    }, [onSignatureChange]);

    useImperativeHandle(ref, () => ({ clear: clearCanvas }), [clearCanvas]);

    const getCanvasCoords = useCallback((clientX: number, clientY: number) => {
      const canvas = canvasRef.current;
      if (!canvas) return { x: 0, y: 0 };

      const rect = canvas.getBoundingClientRect();
      const scaleX = canvas.width / rect.width;
      const scaleY = canvas.height / rect.height;
      return {
        x: (clientX - rect.left) * scaleX,
        y: (clientY - rect.top) * scaleY,
      };
    }, []);

    const stopDrawing = useCallback(() => {
      if (!isDrawingRef.current) return;
      isDrawingRef.current = false;

      const canvas = canvasRef.current;
      if (!canvas) return;

      onSignatureChange(canvas.toDataURL("image/png"));
    }, [onSignatureChange]);

    const handleMouseMove = useCallback(
      (e: MouseEvent) => {
        if (!isDrawingRef.current) return;

        const canvas = canvasRef.current;
        if (!canvas) return;

        const ctx = canvas.getContext("2d");
        if (!ctx) return;

        const { x, y } = getCanvasCoords(e.clientX, e.clientY);
        ctx.lineTo(x, y);
        ctx.stroke();
      },
      [getCanvasCoords]
    );

    const handleMouseUp = useCallback(() => {
      stopDrawing();
    }, [stopDrawing]);

    const setupCanvas = useCallback(() => {
      const canvas = canvasRef.current;
      if (!canvas) return;

      const ctx = canvas.getContext("2d");
      if (!ctx) return;

      // Fixed output size regardless of visual canvas size
      const outputWidth = 1030;
      const outputHeight = 160;
      canvas.width = outputWidth;
      canvas.height = outputHeight;

      ctx.strokeStyle = "#012c1c";
      ctx.lineWidth = 4;
      ctx.lineCap = "round";
      ctx.lineJoin = "round";
    }, []);

    useEffect(() => {
      setupCanvas();
    }, [setupCanvas]);

    useEffect(() => {
      document.addEventListener("mousemove", handleMouseMove);
      document.addEventListener("mouseup", handleMouseUp);

      return () => {
        document.removeEventListener("mousemove", handleMouseMove);
        document.removeEventListener("mouseup", handleMouseUp);
      };
    }, [handleMouseMove, handleMouseUp]);

    const startDrawing = useCallback(
      (e: React.MouseEvent | React.TouchEvent) => {
        const canvas = canvasRef.current;
        if (!canvas) return;

        isDrawingRef.current = true;
        const ctx = canvas.getContext("2d");
        if (!ctx) return;

        let clientX: number, clientY: number;
        if ("touches" in e) {
          clientX = e.touches[0].clientX;
          clientY = e.touches[0].clientY;
        } else {
          clientX = e.clientX;
          clientY = e.clientY;
        }

        const { x, y } = getCanvasCoords(clientX, clientY);
        ctx.beginPath();
        ctx.moveTo(x, y);
      },
      [getCanvasCoords]
    );

    const drawTouch = useCallback(
      (e: React.TouchEvent) => {
        if (!isDrawingRef.current) return;

        const canvas = canvasRef.current;
        if (!canvas) return;

        const ctx = canvas.getContext("2d");
        if (!ctx) return;

        const { x, y } = getCanvasCoords(
          e.touches[0].clientX,
          e.touches[0].clientY
        );
        ctx.lineTo(x, y);
        ctx.stroke();
      },
      [getCanvasCoords]
    );

    return (
      <SignatureCanvas
        ref={canvasRef}
        onMouseDown={startDrawing}
        onTouchStart={startDrawing}
        onTouchMove={drawTouch}
        onTouchEnd={stopDrawing}
      />
    );
  }
);

export { SignaturePad };
