import React, { useMemo } from "react";
import { useStaticQuery, graphql } from "gatsby";

import {
  Container,
  Section,
  Title,
  TitleWrap,
  Card,
  StyledImage,
  StyledIcon,
  Slider,
  SliderTrack,
} from "./Style";

function Support() {
  const data = useStaticQuery(graphql`
    query {
      molochdao: file(relativePath: { eq: "images/support/molochdao.webp" }) {
        childImageSharp {
          fluid(maxWidth: 180, quality: 100) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      vitalik: file(relativePath: { eq: "images/support/vitalik.webp" }) {
        childImageSharp {
          fluid(maxWidth: 180, quality: 100) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `);

  const supportsImages = [
    {
      alt: "Vitalik Buterin image",
      fluid: data.vitalik.childImageSharp.fluid,
      href: "https://x.com/VitalikButerin/status/1882063623499211016",
    },
    {
      alt: "MolochDao image",
      fluid: data.molochdao.childImageSharp.fluid,
      width: "20rem",
      href: "https://app.daohaus.club/dao/0x1/0x519f9662798c2e07fbd5b30c1445602320c5cf5b/proposals/43",
    },
  ] as const;

  const supportsIcons = [
    { name: "nouns", size: "fill", href: "https://nouns.wtf/vote/502" },
    {
      name: "alchemix",
      size: "fill",
      href: "https://x.com/AlchemixFi/status/1793924000965431668",
    },
    {
      name: "ethFoundation",
      size: "fill",
      href: "https://ethereum.foundation/",
    },
    {
      name: "giveEth",
      size: "fill",
      href: "https://giveth.io/project/defend-roman-storm",
    },
    {
      name: "paradigm",
      size: "fill",
      href: "https://x.com/matthuang/status/1884358182082163162",
    },
    {
      name: "bankless",
      size: "fill",
      href: "https://www.bankless.com/podcast/roman-storm-speaks-tornado-cash-developer-on-whats-at-stake",
    },
    {
      name: "juicebox",
      size: "fill",
      href: "https://juicebox.money/@defend-roman-storm",
    },
    { name: "logo", size: "fill", href: "https://wewantjusticedao.org/" },
  ] as const;

  const SliderContent = () => (
    <>
      {supportsImages.map(({ href, ...support }, index) => (
        <Card key={`${support.alt}-${index}`} href={href}>
          <StyledImage {...support} />
        </Card>
      ))}
      {supportsIcons.map(({ href, ...support }, index) => (
        <Card key={`${support.name}-${index}`} href={href}>
          <StyledIcon {...support} />
        </Card>
      ))}
    </>
  );

  return (
    <Section id="support">
      <Container>
        <TitleWrap>
          <Title>We have support from</Title>
        </TitleWrap>

        <Slider>
          <SliderTrack>
            <SliderContent />
            <SliderContent />
          </SliderTrack>
        </Slider>
      </Container>
    </Section>
  );
}

export { Support };
