import React from "react";
import { useStaticQuery, graphql } from "gatsby";

import { link } from "constant";

import { Icon } from "components/ui";

import {
  Wrap,
  Info,
  Title,
  Action,
  ImgWrap,
  Section,
  InfoWrap,
  InfoText,
  Container,
  ActionText,
  DonateLink,
  Composition,
  ActionTitle,
  StyledImage,
  ActionContent,
} from "./Style";

const Whois = () => {
  const data = useStaticQuery(graphql`
    query {
      roman: file(relativePath: { eq: "images/whois/whois-roman.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 307, quality: 100) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `);

  return (
    <Section id="whoIs">
      <Wrap>
        <Container>
          <Info>
            <Title>Who is Roman Storm?</Title>
            <InfoWrap>
              <InfoText>
                With a&nbsp;strong background in&nbsp;software development and
                blockchain technology, Roman Storm has been a&nbsp;leading
                innovator and vocal advocate for privacy in&nbsp;the blockchain
                ecosystem. Storm&rsquo;s work has made significant contributions
                to&nbsp;ongoing discussions about the balance between privacy
                and regulatory compliance in&nbsp;the evolving landscape
                of&nbsp;cryptocurrencies.
              </InfoText>
              <InfoText>
                Despite facing criminal charges, Storm is&nbsp;committed
                to&nbsp;challenging the Southern District of&nbsp;New York
                prosecutors&rsquo; unprecedented and overly expansive theories
                of&nbsp;criminal liability and advancing the principles
                of&nbsp;decentralization and personal privacy.
              </InfoText>
              <InfoText>
                Help Roman in&nbsp;his fight against the criminalization
                of&nbsp;software development and join him in&nbsp;standing
                up&nbsp;for the right to&nbsp;privacy!
              </InfoText>
            </InfoWrap>

            <Composition>
              <ImgWrap>
                <StyledImage
                  fluid={data.roman.childImageSharp.fluid}
                  alt="RomanStorm image"
                />
              </ImgWrap>
            </Composition>
          </Info>
        </Container>
        <Action>
          <ActionContent>
            <ActionTitle>Your chance to help!</ActionTitle>
            <ActionText>
              Lobby the Trump administration to <strong>#FreeRomanStorm</strong>
            </ActionText>
          </ActionContent>

          <DonateLink href={link.TWEET}>
            Share and Like
            <Icon name="arrow" rotate="-90deg" />
          </DonateLink>
        </Action>
      </Wrap>
    </Section>
  );
};

export { Whois };
