import React, { useState } from "react";

import { letter as letterConstants } from "constant";
import { letter, LetterValidationErrors } from "utils";

import { Icon } from "components/ui";
import { FloatingMessages } from "components/feature";

import { FormData, Country } from "./types";

import { StateSelect } from "./StateSelect";
import { CountrySelect } from "./CountrySelect";

import {
  Title,
  Section,
  Subtitle,
  Container,
  TitleWrap,
  LetterForm,
  FieldError,
  LetterCard,
  HeaderIcons,
  ChevronIcon,
  ErrorMessage,
  LetterCardTo,
  AvatarCircle,
  MailIconCircle,
  LetterCardDate,
  LetterCardLabel,
  LetterCardInput,
  LetterCardFooter,
  SendLetterButton,
  LetterCardHeader,
  LetterCardFormRow,
  LetterCardTextArea,
  LetterCardFormGroup,
  HeaderIconsWrapper,
} from "../Style";

interface FormStepProps {
  formData: FormData;
  currentDate: string;
  countries: Country[];
  isLoadingLocation: boolean;
  loadError?: string | null;
  onChange: (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => void;
  onCountryChange: (country: Country) => void;
  onPreview: VoidFunction;
}

const ChevronsRightSvg = () => (
  <svg viewBox="0 0 64 64" fill="none" xmlns="http://www.w3.org/2000/svg">
    <path
      d="M34.67 18.67L48 32L34.67 45.33"
      strokeLinecap="round"
      strokeLinejoin="round"
    />
    <path
      d="M16 18.67L29.33 32L16 45.33"
      strokeLinecap="round"
      strokeLinejoin="round"
    />
  </svg>
);

const FormStep: React.FC<FormStepProps> = ({
  formData,
  countries,
  currentDate,
  isLoadingLocation,
  loadError,
  onChange,
  onCountryChange,
  onPreview,
}) => {
  const [errors, setErrors] = useState<LetterValidationErrors>({});
  const [touched, setTouched] = useState<Record<string, boolean>>({});

  const isUSA = formData.country === "United States";

  const handleBlur = (field: string) => {
    setTouched((prev) => ({ ...prev, [field]: true }));
    const validationErrors = letter.validateForm(formData);
    setErrors((prev) => ({
      ...prev,
      [field]: validationErrors[field as keyof LetterValidationErrors],
    }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    const validationErrors = letter.validateForm(formData);
    setErrors(validationErrors);
    setTouched({
      fullName: true,
      email: true,
      city: true,
      country: true,
      state: true,
      zipCode: true,
      message: true,
    });

    if (letter.isFormValid(validationErrors)) {
      onPreview();
    } else {
      const fieldOrder = ["message", "fullName", "email", "city", "country", "state", "zipCode"];
      const firstErrorField = fieldOrder.find(
        (field) => validationErrors[field as keyof typeof validationErrors]
      );
      if (firstErrorField) {
        document
          .getElementById(firstErrorField)
          ?.scrollIntoView({ behavior: "smooth", block: "center" });
      }
    }
  };

  const getFieldError = (
    field: keyof LetterValidationErrors
  ): string | undefined => {
    if (!touched[field]) return undefined;
    return errors[field];
  };

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    onChange(e);
    const { name } = e.target;
    if (touched[name]) {
      const newFormData = { ...formData, [name]: e.target.value };
      const validationErrors = letter.validateForm(newFormData);
      setErrors((prev) => ({
        ...prev,
        [name]: validationErrors[name as keyof LetterValidationErrors],
      }));
    }
  };

  const handleStateChange = (state: string) => {
    const syntheticEvent = {
      target: { name: "state", value: state },
    } as React.ChangeEvent<HTMLInputElement>;
    handleChange(syntheticEvent);
  };

  const handleCountryChangeWithValidation = (country: Country) => {
    onCountryChange(country);

    const isNewCountryUSA = country.name === "United States";
    const wasUSA = formData.country === "United States";

    // Clear state and zipCode errors when switching from USA
    if (wasUSA && !isNewCountryUSA) {
      setErrors((prev) => ({
        ...prev,
        state: undefined,
        zipCode: undefined,
      }));
      setTouched((prev) => ({
        ...prev,
        state: false,
        zipCode: false,
      }));
    }

    if (touched.country) {
      const newFormData = { ...formData, country: country.name };
      const validationErrors = letter.validateForm(newFormData);
      setErrors((prev) => ({
        ...prev,
        country: validationErrors.country,
      }));
    }
  };

  return (
    <Section>
      <FloatingMessages />
      <Container>
        <HeaderIcons>
          <HeaderIconsWrapper>
            <MailIconCircle>
              <Icon name="mail" size="fill" />
            </MailIconCircle>
            <ChevronIcon>
              <ChevronsRightSvg />
            </ChevronIcon>
            <AvatarCircle>
              <img src="/images/whois/whois-roman.jpg" alt="Roman Storm" />
            </AvatarCircle>
          </HeaderIconsWrapper>
        </HeaderIcons>

        <TitleWrap>
          <Title>Write Your Letter of Support</Title>
          <Subtitle>
            Share why you believe in Roman Storm and his fight for software
            freedom. Your voice matters in the fight for justice.
          </Subtitle>
        </TitleWrap>

        {loadError && <ErrorMessage>{loadError}</ErrorMessage>}

        <LetterForm onSubmit={handleSubmit}>
          <LetterCard>
            <LetterCardHeader>
              <LetterCardTo>Letter of Support for Roman Storm</LetterCardTo>
              <LetterCardDate>{currentDate}</LetterCardDate>
            </LetterCardHeader>

            <LetterCardFormGroup>
              <LetterCardLabel>Your Message</LetterCardLabel>
              <LetterCardTextArea
                id="message"
                name="message"
                value={formData.message}
                onChange={handleChange}
                onBlur={() => handleBlur("message")}
                placeholder={letterConstants.DEFAULT_MESSAGE_PLACEHOLDER}
                $hasError={!!getFieldError("message")}
              />
              {getFieldError("message") && (
                <FieldError>{getFieldError("message")}</FieldError>
              )}
            </LetterCardFormGroup>

            <LetterCardFormRow>
              <LetterCardFormGroup>
                <LetterCardLabel>Full Name</LetterCardLabel>
                <LetterCardInput
                  type="text"
                  id="fullName"
                  name="fullName"
                  value={formData.fullName}
                  onChange={handleChange}
                  onBlur={() => handleBlur("fullName")}
                  placeholder="John Doe"
                  autoComplete="name"
                  $hasError={!!getFieldError("fullName")}
                />
                {getFieldError("fullName") && (
                  <FieldError>{getFieldError("fullName")}</FieldError>
                )}
              </LetterCardFormGroup>
              <LetterCardFormGroup>
                <LetterCardLabel>Email</LetterCardLabel>
                <LetterCardInput
                  type="email"
                  id="email"
                  name="email"
                  value={formData.email}
                  onChange={handleChange}
                  onBlur={() => handleBlur("email")}
                  placeholder="example@mail.com"
                  autoComplete="email"
                  $hasError={!!getFieldError("email")}
                />
                {getFieldError("email") && (
                  <FieldError>{getFieldError("email")}</FieldError>
                )}
              </LetterCardFormGroup>
            </LetterCardFormRow>

            <LetterCardFormRow>
              <LetterCardFormGroup>
                <LetterCardLabel>City</LetterCardLabel>
                <LetterCardInput
                  type="text"
                  id="city"
                  name="city"
                  value={formData.city}
                  onChange={handleChange}
                  onBlur={() => handleBlur("city")}
                  placeholder={isLoadingLocation ? "Detecting..." : "New York"}
                  autoComplete="address-level2"
                  $hasError={!!getFieldError("city")}
                />
                {getFieldError("city") && (
                  <FieldError>{getFieldError("city")}</FieldError>
                )}
              </LetterCardFormGroup>
              <LetterCardFormGroup>
                <LetterCardLabel>Country</LetterCardLabel>
                <CountrySelect
                  countries={countries}
                  value={formData.country}
                  onChange={handleCountryChangeWithValidation}
                  isLoading={isLoadingLocation}
                  hasError={!!getFieldError("country")}
                />
                {getFieldError("country") && (
                  <FieldError>{getFieldError("country")}</FieldError>
                )}
              </LetterCardFormGroup>
            </LetterCardFormRow>

            {isUSA && (
              <LetterCardFormRow>
                <LetterCardFormGroup>
                  <LetterCardLabel>State</LetterCardLabel>
                  <StateSelect
                    value={formData.state}
                    onChange={handleStateChange}
                    hasError={!!getFieldError("state")}
                  />
                  {getFieldError("state") && (
                    <FieldError>{getFieldError("state")}</FieldError>
                  )}
                </LetterCardFormGroup>
                <LetterCardFormGroup>
                  <LetterCardLabel>ZIP Code</LetterCardLabel>
                  <LetterCardInput
                    type="text"
                    id="zipCode"
                    name="zipCode"
                    value={formData.zipCode}
                    onChange={handleChange}
                    onBlur={() => handleBlur("zipCode")}
                    placeholder="10001"
                    autoComplete="postal-code"
                    maxLength={10}
                    $hasError={!!getFieldError("zipCode")}
                  />
                  {getFieldError("zipCode") && (
                    <FieldError>{getFieldError("zipCode")}</FieldError>
                  )}
                </LetterCardFormGroup>
              </LetterCardFormRow>
            )}

            <LetterCardFooter>
              <SendLetterButton type="submit">
                Preview Letter
                <Icon name="arrow" rotate="-90deg" />
              </SendLetterButton>
            </LetterCardFooter>
          </LetterCard>
        </LetterForm>
      </Container>
    </Section>
  );
};

export { FormStep };
