import React, { useState, useEffect } from "react";
import { Icon } from "components/ui";
import {
  QuoteMark,
  MessageText,
  MessageCard,
  LetterNumber,
  MessageContainer,
} from "./Style";

const MESSAGES = [
  "your strength and your fight inspire all of us to stand for developers' freedom.",
  "you're not fighting just for yourself, but for everyone who believes in the right to write code.",
  "your courage in the face of pressure shows what real integrity looks like.",
  "freedom to develop is essential, and you've reminded the world why it matters.",
  "Tornado Cash became a symbol of privacy, and privacy is a fundamental right.",
  "even under immense pressure, you never gave up on your principles.",
  "your resilience proves that innovation should never be criminalized.",
  "the way you continue to grow, train, and stay balanced is truly inspiring.",
  "being a loving father while standing for your beliefs speaks volumes about who you are.",
  "you represent the idea that code is speech, and speech must be free.",
  "defending privacy is not a crime, and your case reminds us of that every day.",
  "your story gives strength to developers who fear building meaningful technology.",
  "you show that even in the hardest moments, humanity and dignity matter.",
  "we stand with you, because your freedom affects the freedom of us all.",
  "your work shaped the future of decentralized and privacy-preserving tech.",
  "your determination encourages others not to stay silent.",
  "thank you for standing up for the right to build without fear.",
  "you've shown the world that privacy is a necessity, not a luxury.",
  "your courage reminds us why decentralization exists in the first place.",
  "you are a symbol of resilience, freedom, and responsibility in tech.",
];

interface FloatingMessagesProps {
  startNumber?: number;
}

const FloatingMessages: React.FC<FloatingMessagesProps> = ({
  startNumber = 13,
}) => {
  const [visibleMessages, setVisibleMessages] = useState<
    Array<{
      id: number;
      message: string;
      letterNumber: number;
      position: { top: number; side: "left" | "right" };
    }>
  >([]);

  useEffect(() => {
    let messageCounter = 0;
    let letterCounter = startNumber;

    const addMessage = () => {
      setVisibleMessages((prev) => {
        const leftCount = prev.filter(
          (msg) => msg.position.side === "left"
        ).length;
        const rightCount = prev.filter(
          (msg) => msg.position.side === "right"
        ).length;

        // Determine which side to use
        let side: "left" | "right";
        if (leftCount === 0 && rightCount === 0) {
          // No messages, choose randomly
          side = Math.random() < 0.5 ? "left" : "right";
        } else if (leftCount === 0) {
          // No message on left, use left
          side = "left";
        } else if (rightCount === 0) {
          // No message on right, use right
          side = "right";
        } else {
          // Both sides occupied, don't add a new message
          return prev;
        }

        const message = MESSAGES[messageCounter % MESSAGES.length];
        const newMessage = {
          id: Date.now() + Math.random(),
          message,
          letterNumber: letterCounter,
          position: {
            top: Math.random() * 30 + 45, // 45% to 75% from top (textarea level only)
            side,
          },
        };

        messageCounter++;
        letterCounter++;

        // Remove message after 5 seconds
        setTimeout(() => {
          setVisibleMessages((current) =>
            current.filter((msg) => msg.id !== newMessage.id)
          );
        }, 5000);

        return [...prev, newMessage];
      });
    };

    addMessage();

    const interval = setInterval(() => {
      addMessage();
    }, Math.random() * 2000 + 3000);

    return () => clearInterval(interval);
  }, [startNumber]);

  return (
    <MessageContainer>
      {visibleMessages.map((msg) => (
        <MessageCard
          key={msg.id}
          $side={msg.position.side}
          style={{
            top: `${msg.position.top}%`,
          }}
        >
          <LetterNumber>Letter #{msg.letterNumber}</LetterNumber>
          <MessageText>{msg.message}</MessageText>
          <QuoteMark>
            <Icon name="quote" />
          </QuoteMark>
        </MessageCard>
      ))}
    </MessageContainer>
  );
};

export { FloatingMessages };
