import React, { useRef, useEffect, useState } from "react";

import {
  SelectOption,
  SelectDropdown,
  SelectNoResults,
  LetterCardInput,
  LetterSelectWrapper,
} from "../Style";

import { Country } from "./types";

interface CountrySelectProps {
  countries: Country[];
  value: string;
  onChange: (country: Country) => void;
  isLoading?: boolean;
  hasError?: boolean;
}

const CountrySelect: React.FC<CountrySelectProps> = ({
  countries,
  value,
  onChange,
  isLoading = false,
  hasError = false,
}) => {
  const [search, setSearch] = useState(value);
  const [isOpen, setIsOpen] = useState(false);
  const wrapperRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    setSearch(value);
  }, [value]);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (
        wrapperRef.current &&
        !wrapperRef.current.contains(event.target as Node)
      ) {
        setIsOpen(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = e.target.value;
    setSearch(newValue);
    setIsOpen(true);
    // Update form value as user types (custom input)
    onChange({ name: newValue, code: "" });
  };

  const handleSelect = (country: Country) => {
    onChange(country);
    setSearch(country.name);
    setIsOpen(false);
  };

  const handleBlur = () => {
    setTimeout(() => {
      if (!wrapperRef.current?.contains(document.activeElement)) {
        setIsOpen(false);
      }
    }, 150);
  };

  const filteredCountries = countries.filter((country) =>
    country.name.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <LetterSelectWrapper ref={wrapperRef}>
      <LetterCardInput
        type="text"
        id="country"
        value={search}
        onChange={handleSearch}
        onFocus={() => setIsOpen(true)}
        onBlur={handleBlur}
        placeholder={isLoading ? "Detecting..." : "Select country"}
        autoComplete="off"
        $hasError={hasError}
      />
      <SelectDropdown isOpen={isOpen}>
        {filteredCountries.length > 0 ? (
          filteredCountries.map((country) => (
            <SelectOption
              key={country.code}
              onClick={() => handleSelect(country)}
              isHighlighted={value === country.name}
            >
              {country.name}
            </SelectOption>
          ))
        ) : (
          <SelectNoResults>No countries found</SelectNoResults>
        )}
      </SelectDropdown>
    </LetterSelectWrapper>
  );
};

export { CountrySelect };
