import React, { ChangeEvent, useState } from "react";

import axios from "axios";

import { MEDIA_INQUIRY_API } from "constant";
import { validateForm } from "utils/validation";

import { Button } from "components/ui";

import {
  Wrap,
  Form,
  Title,
  Error,
  Input,
  Section,
  IconWrap,
  TextArea,
  InputWrap,
  Container,
  StyledIcon,
  InputLabel,
  StatusBlock,
  StatusTitle,
  RequiredField,
  StatusDescription,
} from "./Style";

type FormType = {
  name: string;
  email: string;
  message: string;
  mediaOrganisation: string;
};

const MediaInquiry = () => {
  const [isSuccess, setIsSuccess] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [requestError, setRequestError] = useState("");

  const [errors, setErrors] = useState<Record<string, string>>({});

  const [form, setForm] = useState<FormType>({
    name: "",
    email: "",
    message: "",
    mediaOrganisation: "",
  });

  const onHandleChange = (
    e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    if (name in form) {
      setForm((prev) => ({ ...prev, [name]: value }));
    }
  };

  const onHandleSubmit = async (e: ChangeEvent<HTMLFormElement>) => {
    try {
      setIsLoading(true);
      setRequestError("");

      e.preventDefault();
      const validationErrors = validateForm(form);

      if (Object.keys(validationErrors).length > 0) {
        setErrors(validationErrors);
      } else {
        setErrors({});

        await axios.post(MEDIA_INQUIRY_API, form);
        setIsSuccess(true);
      }
    } catch (error) {
      setRequestError(error.message);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Section>
      <Wrap>
        <Container>
          <Title>Media Inquiry</Title>
          {isSuccess ? (
            <StatusBlock>
              <IconWrap>
                <StyledIcon name="check" size="fill" />
              </IconWrap>
              <StatusTitle>Successfully sent</StatusTitle>
              <StatusDescription>
                Team will get in touch with you soon!
              </StatusDescription>
            </StatusBlock>
          ) : (
            <Form onSubmit={onHandleSubmit}>
              <InputWrap>
                <InputLabel htmlFor="name">
                  Name <RequiredField>*</RequiredField>
                </InputLabel>
                <Input
                  id="name"
                  name="name"
                  value={form.name}
                  onChange={onHandleChange}
                  withError={Boolean(errors.name)}
                />
                <Error>{errors.name || ""}</Error>
              </InputWrap>
              <InputWrap>
                <InputLabel htmlFor="email">
                  E-mail <RequiredField>*</RequiredField>
                </InputLabel>
                <Input
                  id="email"
                  name="email"
                  value={form.email}
                  onChange={onHandleChange}
                  withError={Boolean(errors.email)}
                />
                <Error>{errors.email || ""}</Error>
              </InputWrap>
              <InputWrap>
                <InputLabel htmlFor="mediaOrganisation">
                  Media organisation <RequiredField>*</RequiredField>
                </InputLabel>
                <Input
                  id="mediaOrganisation"
                  name="mediaOrganisation"
                  value={form.mediaOrganisation}
                  onChange={onHandleChange}
                  withError={Boolean(errors.mediaOrganisation)}
                />
                <Error>{errors.mediaOrganisation || ""}</Error>
              </InputWrap>
              <InputWrap>
                <InputLabel htmlFor="message">
                  Message <RequiredField>*</RequiredField>
                </InputLabel>
                <TextArea
                  id="message"
                  name="message"
                  value={form.message}
                  onChange={onHandleChange}
                  withError={Boolean(errors.message)}
                />
                <Error>{errors.message || ""}</Error>
              </InputWrap>
              <Button submit isLoading={isLoading}>
                Send
              </Button>
              <Error>{requestError}</Error>
            </Form>
          )}
        </Container>
      </Wrap>
    </Section>
  );
};

export { MediaInquiry };
