import React from "react";
import { Link } from "react-scroll";

type Props = {
  to: string;
  href?: string;
  offset?: number;
  children?: React.ReactNode;
};

function ScrollTo({ href, to, children, offset = -50 }: Props) {
  const handleClick = () => {
    if (href && window.location.pathname !== "/") {
      window.location.href = href;
    }
  };

  return (
    <Link
      href={href}
      spy
      hashSpy
      smooth
      to={to}
      offset={offset}
      onClick={handleClick}
    >
      {children}
    </Link>
  );
}

export { ScrollTo };
