import React, { ReactNode } from "react";
import { Container, Grid, Section, Title, TitleWrap, SubTitle } from "./Style";

type Props = {
  id: string;
  title?: string;
  subtitle?: string;
  children: ReactNode;
  withPaddings?: boolean;
};
export function RowSections({
  id,
  title,
  subtitle,
  children,
  withPaddings = true,
}: Props) {
  return (
    <Section id={id} withPaddings={withPaddings}>
      <Container>
        <TitleWrap>
          {title && <Title>{title}</Title>}
          {subtitle && <SubTitle>{subtitle}</SubTitle>}
        </TitleWrap>

        <Grid>{children}</Grid>
      </Container>
    </Section>
  );
}
