import React, { useRef, useEffect, useState } from "react";

import {
  SelectOption,
  SelectDropdown,
  SelectNoResults,
  LetterCardInput,
  LetterSelectWrapper,
} from "../Style";

const US_STATES = [
  { name: "Alabama", code: "AL" },
  { name: "Alaska", code: "AK" },
  { name: "Arizona", code: "AZ" },
  { name: "Arkansas", code: "AR" },
  { name: "California", code: "CA" },
  { name: "Colorado", code: "CO" },
  { name: "Connecticut", code: "CT" },
  { name: "Delaware", code: "DE" },
  { name: "Florida", code: "FL" },
  { name: "Georgia", code: "GA" },
  { name: "Hawaii", code: "HI" },
  { name: "Idaho", code: "ID" },
  { name: "Illinois", code: "IL" },
  { name: "Indiana", code: "IN" },
  { name: "Iowa", code: "IA" },
  { name: "Kansas", code: "KS" },
  { name: "Kentucky", code: "KY" },
  { name: "Louisiana", code: "LA" },
  { name: "Maine", code: "ME" },
  { name: "Maryland", code: "MD" },
  { name: "Massachusetts", code: "MA" },
  { name: "Michigan", code: "MI" },
  { name: "Minnesota", code: "MN" },
  { name: "Mississippi", code: "MS" },
  { name: "Missouri", code: "MO" },
  { name: "Montana", code: "MT" },
  { name: "Nebraska", code: "NE" },
  { name: "Nevada", code: "NV" },
  { name: "New Hampshire", code: "NH" },
  { name: "New Jersey", code: "NJ" },
  { name: "New Mexico", code: "NM" },
  { name: "New York", code: "NY" },
  { name: "North Carolina", code: "NC" },
  { name: "North Dakota", code: "ND" },
  { name: "Ohio", code: "OH" },
  { name: "Oklahoma", code: "OK" },
  { name: "Oregon", code: "OR" },
  { name: "Pennsylvania", code: "PA" },
  { name: "Rhode Island", code: "RI" },
  { name: "South Carolina", code: "SC" },
  { name: "South Dakota", code: "SD" },
  { name: "Tennessee", code: "TN" },
  { name: "Texas", code: "TX" },
  { name: "Utah", code: "UT" },
  { name: "Vermont", code: "VT" },
  { name: "Virginia", code: "VA" },
  { name: "Washington", code: "WA" },
  { name: "West Virginia", code: "WV" },
  { name: "Wisconsin", code: "WI" },
  { name: "Wyoming", code: "WY" },
  { name: "District of Columbia", code: "DC" },
];

interface StateSelectProps {
  value: string;
  onChange: (state: string) => void;
  hasError?: boolean;
}

const StateSelect: React.FC<StateSelectProps> = ({
  value,
  onChange,
  hasError = false,
}) => {
  const [search, setSearch] = useState(value);
  const [isOpen, setIsOpen] = useState(false);
  const wrapperRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    setSearch(value);
  }, [value]);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (
        wrapperRef.current &&
        !wrapperRef.current.contains(event.target as Node)
      ) {
        setIsOpen(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearch(e.target.value);
    setIsOpen(true);
  };

  const handleSelect = (state: { name: string; code: string }) => {
    onChange(state.name);
    setSearch(state.name);
    setIsOpen(false);
  };

  const filteredStates = US_STATES.filter((state) =>
    state.name.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <LetterSelectWrapper ref={wrapperRef}>
      <LetterCardInput
        type="text"
        id="state"
        value={search}
        onChange={handleSearch}
        onFocus={() => setIsOpen(true)}
        placeholder="Select state"
        autoComplete="off"
        $hasError={hasError}
      />
      <SelectDropdown isOpen={isOpen}>
        {filteredStates.length > 0 ? (
          filteredStates.map((state) => (
            <SelectOption
              key={state.code}
              onClick={() => handleSelect(state)}
              isHighlighted={value === state.name}
            >
              {state.name}
            </SelectOption>
          ))
        ) : (
          <SelectNoResults>No states found</SelectNoResults>
        )}
      </SelectDropdown>
    </LetterSelectWrapper>
  );
};

export { StateSelect };
