import styled from "styled-components/macro";
import { Link } from "gatsby";
import { transparentize } from "polished";

export const Section = styled.section`
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: ${(props) => props.theme.color.elementBg};
  overflow: hidden;
`;

export const Container = styled.div`
  padding: 6rem 1.6rem;
  width: 100%;
  max-width: 124rem;
  display: flex;
  margin: 0 auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  z-index: ${(props) => props.theme.zIndex.minimal};

  ${(props) => props.theme.breakpoints.greaterThan("sm")`
    padding: 8rem 3.2rem;
  `};

  ${(props) => props.theme.breakpoints.greaterThan("md")`
    flex-direction: row;
    justify-content: space-between;
    gap: 6rem;
    padding: 10rem 3.2rem;
  `};
`;

export const ContentWrap = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.6rem;
  text-align: center;
  flex: 1;
  max-width: 48rem;

  ${(props) => props.theme.breakpoints.greaterThan("md")`
    align-items: flex-start;
    text-align: left;
  `};
`;

export const Title = styled.h2`
  margin: 0;
  padding: 0;
  font-family: ${(props) => props.theme.font.family.default};
  font-weight: ${(props) => props.theme.font.weight.bold};
  font-size: clamp(3.2rem, 8vw, 6.4rem);
  line-height: 1.01;
  letter-spacing: -0.03em;
  color: ${(props) => props.theme.color.text};
`;

export const Highlight = styled.span`
  display: block;
  font-family: ${(props) => props.theme.font.family.default};
  font-weight: ${(props) => props.theme.font.weight.bold};
  font-size: clamp(2rem, 5vw, 3.6rem);
  line-height: 1.7;
  color: ${(props) => props.theme.color.success};
  margin-top: 0.8rem;
`;

export const Subtitle = styled.p`
  margin: 0;
  padding: 0;
  font-size: 1.6rem;
  font-weight: ${(props) => props.theme.font.weight.medium};
  line-height: 1.5;
  letter-spacing: -0.019em;
  color: ${(props) => props.theme.color.text};
  max-width: 47.4rem;

  ${(props) => props.theme.breakpoints.greaterThan("sm")`
    font-size: 1.8rem;
  `};
`;

export const CTAButton = styled(Link)`
  margin-top: 1.2rem;
  padding: 1.2rem 2rem 1.2rem 3rem;
  display: inline-flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  font-family: ${(props) => props.theme.font.family.default};
  font-weight: ${(props) => props.theme.font.weight.semiBold};
  font-size: 1.6rem;
  line-height: 0.85;
  letter-spacing: -0.03em;
  text-decoration: none;
  color: ${(props) => props.theme.color.white};
  background-color: ${(props) => props.theme.color.success};
  border: none;
  border-radius: 4.9rem;
  cursor: pointer;
  transition: all 0.26s ease;

  svg {
    width: 2.4rem;
    height: 2.4rem;
    fill: ${(props) => props.theme.color.white};
    transition: transform 0.26s ease;
  }

  &:hover {
    box-shadow: 0 1rem 1.8rem -0.9rem ${(props) => transparentize(0.63, props.theme.color.black)};

    svg {
      transform: rotate(-45deg);
    }
  }
`;

export const IllustrationWrap = styled.div`
  position: relative;
  width: 26rem;
  height: 34rem;
  flex-shrink: 0;

  ${(props) => props.theme.breakpoints.greaterThan("sm")`
    width: 33.5rem;
    height: 44.6rem;
  `};
`;

export const MailIcon = styled.div`
  width: 6rem;
  height: 6rem;

  svg {
    width: 100%;
    height: 100%;
    stroke: ${(props) => props.theme.color.success};
    stroke-width: 6;
    fill: none;
  }

  ${(props) => props.theme.breakpoints.greaterThan("sm")`
    width: 7.2rem;
    height: 7.2rem;
  `};
`;

export const LetterStack = styled.div`
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 26rem;
  height: 36rem;

  ${(props) => props.theme.breakpoints.greaterThan("sm")`
    width: 33.5rem;
    height: 44.6rem;
    left: auto;
    right: 0;
    transform: none;
  `};
`;

