import React from "react";
import { useStaticQuery, graphql } from "gatsby";

import { menu } from "constant";

import { Href, Icon, ScrollTo } from "components/ui";

import {
  Section,
  DonateLink,
  Container,
  Info,
  Title,
  InfoWrap,
  InfoText,
  Composition,
  ImgWrap,
  StyledImage,
  InfoLink,
  InfoAction,
  HashTag,
  InfoSubTitle,
  InfoSubTitle1,
  CompositionDescription,
} from "./Style";

const Intro = () => {
  const data = useStaticQuery(graphql`
    query {
      roman: file(relativePath: { eq: "images/intro/intro-roman.webp" }) {
        childImageSharp {
          fluid(maxWidth: 307, quality: 100) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `);

  return (
    <Section id="intro">
      <Container>
        <Info>
          <Composition>
            <ImgWrap>
              <StyledImage
                fluid={data.roman.childImageSharp.fluid}
                alt="RomanStorm image"
              />
            </ImgWrap>
          </Composition>
          <CompositionDescription>
            <Title>
              Free
              <br />
              Roman
              <br />
              Storm
            </Title>
            <InfoSubTitle1>
              <HashTag translate="no" className="notranslate">
                #FreeRomanStorm
              </HashTag>
            </InfoSubTitle1>
          </CompositionDescription>

          <InfoSubTitle>
            <HashTag translate="no" className="notranslate">
              #FreeRomanStorm
            </HashTag>
          </InfoSubTitle>

          <InfoWrap>
            <InfoText>
              Roman Storm, co-founder of&nbsp;the privacy protocol Tornado Cash,
              was arrested on&nbsp;August&nbsp;23, 2023, and charged by&nbsp;the
              U.S. Attorney&rsquo;s Office for the Southern District of&nbsp;New
              York with conspiracy to&nbsp;commit money laundering, operate
              an&nbsp;unlicensed money transmitting business, and violate
              sanctions. The prosecution alleges his open-source code
              facilitated over $1&nbsp;billion in&nbsp;illicit transactions but
              has raised profound concerns about criminalizing software
              development, free speech, and privacy in&nbsp;decentralized
              systems.
            </InfoText>
            <InfoText>
              In&nbsp;August 2025, after a&nbsp;federal trial, a&nbsp;jury
              returned a&nbsp;partial verdict: guilty on&nbsp;the unlicensed
              money transmitting count (up&nbsp;to&nbsp;5&nbsp;years), with hung
              juries on&nbsp;the money laundering and sanctions counts (each
              up&nbsp;to&nbsp;20&nbsp;years). Post-trial, Storm&rsquo;s team
              moved for acquittal in&nbsp;October 2025; the DOJ opposed
              in&nbsp;November, pushing for a&nbsp;2026 retrial on&nbsp;the
              deadlocked charges. As&nbsp;of&nbsp;January 2026, the motion
              remains pending, an&nbsp;appeal is&nbsp;anticipated, and the fight
              for developer rights and DeFi&rsquo;s future
              continues-strengthened by&nbsp;community support, including the
              Ethereum Foundation&rsquo;s successful $750K matching campaign
              that drove total defense funds over $5&nbsp;million.
            </InfoText>
            <InfoText>
              Help Roman win this fight&nbsp;- donate towards the legal costs,
              or&nbsp;lend your voice with a&nbsp;public letter of&nbsp;support.
            </InfoText>
            <InfoText>
              <strong>
                Your influence can turn the tide for DeFi&rsquo;s future!
              </strong>
            </InfoText>

            <InfoAction>
              <DonateLink to={menu.donate}>
                Help Roman
                <Icon name="arrow" rotate="-90deg" />
              </DonateLink>
              <ScrollTo to={menu.allegations} href="#allegations">
                <InfoLink>Learn More</InfoLink>
              </ScrollTo>
            </InfoAction>
          </InfoWrap>
        </Info>
      </Container>
    </Section>
  );
};

export { Intro };
